@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: Roboto Mono;
  src: url("./assets/Roboto_Mono/RobotoMono-VariableFont_wght.ttf");
}

body {
  margin: 0;
  padding: 0;
}

.popup {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(124, 124, 124, 0.44);
}

.close {
  display: flex;
  justify-content: space-between;
}

.button {
  font-size: 20px;
  cursor: pointer;
}

.popup-content {
  overflow-y: scroll;
  padding: 20px;
  background: #fff;
  width: 80vw;
  height: 90vh;
  border-radius: 5px;
}

.data {
  min-width: 300px;
  max-width: 500px;
  margin-bottom: 10px;
  padding: 0 10px 0 0;
  border-right: 1px solid #000000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form_radio_group {
  display: inline-block;
  overflow: hidden;
}
.form_radio_group-item {
  display: inline-block;
  float: left;
}
.form_radio_group input[type=radio] {
  display: none;
}
.form_radio_group label {
  display: inline-block;
  cursor: pointer;
  padding: 0px 15px;
  line-height: 34px;
  border: 1px solid #999;
  border-right: none;
  user-select: none;
}

.form_radio_group .form_radio_group-item:first-child label {
  border-radius: 6px 0 0 6px;
}
.form_radio_group .form_radio_group-item:last-child label {
  border-radius: 0 6px 6px 0;
  border-right: 1px solid #999;
}

/* Checked */
.form_radio_group input[type=radio]:checked + label {
  background: rgb(47, 189, 97);
  border-color: rgb(47, 189, 97);
  color: white;
  font-weight: bold;
}

/* Hover */
.form_radio_group label:hover {
  color: #666;
}

/* Disabled */
.form_radio_group input[type=radio]:disabled + label {
  background: #efefef;
  color: #666;
}

.cl-btn-7 {
  width: 40px;
  height: 40px;
  border-radius: 40px;
  position: relative;
  z-index: 1;
  margin: 20px 0;
  cursor: pointer;
}
.cl-btn-7:before {
  content: '+';
  color: #16a34a;
  position: absolute;
  z-index: 2;
  transform: rotate(45deg);
  font-size: 50px;
  line-height: 0.9;
  top: -5px;
  left: 6px;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
}
.cl-btn-7:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background: #16a34a;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.77, 0, 0.2, 0.85);
  transform: scale(0.01);
}
.cl-btn-7:hover:after {
  transform: scale(1);
}
.cl-btn-7:hover:before {
  transform: scale(0.8) rotate(45deg);
  color: #fff;
}
